import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Menu } from 'antd';

class Sidenav extends Component {
  constructor() {
    super();
    this.state = {
      selectKey: '0'
    }
  }

  componentWillMount() {
  	this.updateKeyState(this.props.menuData);
  }

  componentWillReceiveProps() {
  	this.updateKeyState(this.props.menuData);
  }

  updateKeyState(data) {
    data.forEach((obj) => {
      let selectMenu = window.location.href.split('/')[4];
      if(obj.path.indexOf(selectMenu) !== -1) {
        this.setState({
          selectKey: obj.keys
        })
      } else if(selectMenu === undefined) {
        this.setState({
          selectKey: '0'
        })
      }
    });
  }

	render() {
		return (
			<Menu
				defaultSelectedKeys={[this.state.selectKey]}
        selectedKeys={[this.state.selectKey]}
				style={{ height: '100%' }}
			>
        {
          this.props.menuData.map((item, index) => {
            return (
              <Menu.Item key={index}>
                <Link to={item.path}>
                  <i className={item.iconName}></i>
                  <span className="sidenav-text">{item.name}</span>
                </Link>
              </Menu.Item>
            );
          })
        }
			</Menu>
		);
	}
}

export default Sidenav;