  import React from 'react';
  import Account from './index';
  import {
    AmountLoader,
    ProfitLoader,
    AssortmentLoader,
    ExportsLoader,
  } from './loadable';
  import Amount from './subpage/amount';
  import Profit from './subpage/profit';
  import Assortment from './subpage/assortment';
  import Exports from './subpage/exports';


  export default {
    path: '/account',
    component: Account,
    childRoutes: [
      { path: '/account/amount', component: AmountLoader },
      { path: '/account/profit', component: ProfitLoader, isIndex: true },
      { path: '/account/assortment', component: AssortmentLoader },
      { path: '/account/exports', component: ExportsLoader }
    ],
  };