  import React from 'react';
  import Loadable from 'react-loadable';
  import Quotations from './index';

  const MyLoadingComponent = ({ isLoading, error }) => {
    return null;
  };

  const StockindexLoader = Loadable.Map({
      loader: {
          Stockindex: () => import('./subpage/stockindex'),
          // widget: () => promise.then(res => res),
      },
      loading: MyLoadingComponent,
      render(loaded, props) {
          let Stockindex = loaded.Stockindex.default;
          // let widgetLod = loaded.widget;
          return <Stockindex {...props} />;
      }
  });

  const ExchangeLoader = Loadable.Map({
      loader: {
          Exchange: () => import('./subpage/exchange'),
          // widget: () => promise.then(res => res),
      },
      loading: MyLoadingComponent,
      render(loaded, props) {
          let Exchange = loaded.Exchange.default;
          // let widgetLod = loaded.widget;
          return <Exchange {...props} />;
      }
  });

  const CalendarLoader = Loadable.Map({
      loader: {
        Calendar: () => import('./subpage/calendar'),
        // widget: () => promise.then(res => res),
      },
      loading: MyLoadingComponent,
      render(loaded, props) {
        let Calendar = loaded.Calendar.default;
        // let widgetLod = loaded.widget;
        return <Calendar {...props} />;
      }
  });

  export default {
    path: '/quotations',
    component: Quotations,
    childRoutes: [
      { path: '/quotations/stockindex', component: StockindexLoader, isIndex: true },
      { path: '/quotations/exchange', component: ExchangeLoader },
      { path: '/quotations/calendar', component: CalendarLoader }
    ],
  };