import React from 'react';
import { LineChart ,Line , XAxis, YAxis, Tooltip,CartesianGrid } from 'recharts';
import Container from 'account/subpage/profit/components/Container';

const LChart = ({ data }) => (
  <Container ratio={1} left>
    <LineChart  data={data}
        margin={{
          top: 0,
          right: 80,
          left: 0,
          bottom: 0,
        }}>
        <CartesianGrid  vertical={false} strokeDasharray="6 6"  />
      <XAxis  tickLine ={false} interval={0} padding={{left:30,right:10}} dataKey="time" stroke="#fff" strokeDasharray="3"/>
      <YAxis  tickLine ={false}  stroke="#fff" orientation='right'strokeDasharray="3" mirror={true} />
      <Tooltip  />
      <Line dataKey="profit" stroke="#10a9bd" dot={false} />
    </LineChart >
  </Container>
);

export default LChart;
